<template>
  <div id="main">
    <div id="header">
      <HeaderComponent />
    </div>

    <div id="content">
      <div v-if="this.loading" class="d-flex align-items-center container">
        <strong>Loading...</strong>
        <div
          class="spinner-border ms-auto"
          role="status"
          aria-hidden="true"
        ></div>
      </div>
      <div v-else class="container">
        <h2 class="container" style="text-align: center">
          หมวดหมู่วิชา: {{ this.posts[0].subjectType }}
        </h2>
        <div class="slider owl-carousel mt-5">
          <div v-for="post in posts" :key="post.id" class="card">
            <div class="img">
              <img :src="post.image" alt="" />
            </div>
            <div class="content">
              <div class="title">{{ post.subjectName }}</div>

              <div class="sub-title">
                <span class="dot-1"></span> {{ post.subjectType }}
              </div>
              <p :inner-html.prop="post.title | truncate(150)"></p>
              <div class="btn">
                <router-link
                  :to="{
                    name: 'course_id',
                    params: { id: post.id },
                  }"
                  ><button>View more</button></router-link
                >
              </div>
            </div>
          </div>
        </div>
        <a
          class="backbt mt-4"
          @click="$router.go(-1)"
          style="text-decoration: none; color: white; cursor: pointer"
          ><i class="fas fa-chevron-left"></i> Back
        </a>
      </div>
    </div>

    <div id="footer">
      <FooterComponent />
    </div>
  </div>
</template>

<script>
import HeaderComponent from "../../components/HeaderComponent";
import FooterComponent from "../../components/FooterComponent";
export default {
  name: "lessonRecent",
  components: { HeaderComponent, FooterComponent },
  data() {
    return {
      posts: [],
      loading: true,
    };
  },
  mounted() {
    this.DataPost();
  },
  methods: {
    DataPost() {
      axios
        .get("/api/content_category/"+ this.$route.params.id)
        .then((res) => {
          this.posts = res.data;
          this.loading = false;
        //   console.log(res.data);
        })
        .catch((err) => {
          this.error = "Error!!";
        });
    },
  },
};

Vue.filter("truncate", function (value, length) {
  if (!value) return "";
  value = value.toString();
  if (value.length > length) {
    return value.substring(0, length) + "...";
  } else {
    return value;
  }
});
</script>

<style>
/* ##### >> https://fonts.google.com/specimen/Sarabun?query=sara#standard-styles */
@import url("https://fonts.googleapis.com/css2?family=Sarabun:wght@300&display=swap");
#content {
  margin-top: 9%;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Sarabun", sans-serif;
}
.slider {
  max-width: 1100px;
  display: flex;
}
.slider .card {
  flex: 1;
  margin: 0 10px;
  background: #fff;
}
.slider .card .img {
  height: 200px;
  width: 100%;
}
.slider .card .img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.slider .card .content {
  padding: 10px 20px;
}
.card .content .title {
  font-size: 25px;
  font-weight: 600;
}
.card .content .sub-title {
  font-size: 20px;
  font-weight: 600;
  color: #e74c3c;
  line-height: 20px;
}
.card .content p {
  text-align: justify;
  margin: 10px 0;
}
.card .content .btn button {
  background: #f36656;
  color: #fff;
  border: none;
  outline: none;
  font-size: 17px;
  padding: 5px 8px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}
.card .content .btn button:hover {
  transform: scale(0.9);
}
.dot-1 {
  height: 18px;
  width: 18px;
  border-radius: 50px;
  background-color: #fe2929;
  display: inline-block;
}
.dot-2 {
  height: 18px;
  width: 18px;
  border-radius: 50px;
  background-color: #add8e6;
  display: inline-block;
}
.dot-3 {
  height: 18px;
  width: 18px;
  border-radius: 50px;
  background-color: #ffa500;
  display: inline-block;
}
.dot-4 {
  height: 18px;
  width: 18px;
  border-radius: 50px;
  background-color: #ff00ff;
  display: inline-block;
}
.backbt {
  width: 80px;
  height: 40px;
  background: #a12c2f;
  border-radius: 2px;
  float: right;
  text-align: center;
  line-height: 40px;
  color: white;
  font-size: 18px;
}
.backbt:hover {
  background: linear-gradient(135deg, #a12c2f, #e43c42);
}
</style>
