___CSS_LOADER_EXPORT___.push([module.id, "#content{margin-top:9%}h1,h2,h3,h4,h5,h6{font-family:Sarabun,sans-serif}.slider{display:flex;max-width:1100px}.slider .card{background:#fff;flex:1;margin:0 10px}.slider .card .img{height:200px;width:100%}.slider .card .img img{height:100%;-o-object-fit:cover;object-fit:cover;width:100%}.slider .card .content{padding:10px 20px}.card .content .title{font-size:25px;font-weight:600}.card .content .sub-title{color:#e74c3c;font-size:20px;font-weight:600;line-height:20px}.card .content p{margin:10px 0;text-align:justify}.card .content .btn button{background:#f36656;border:none;border-radius:5px;color:#fff;cursor:pointer;font-size:17px;outline:none;padding:5px 8px;transition:.3s}.card .content .btn button:hover{transform:scale(.9)}.dot-1{background-color:#fe2929}.dot-1,.dot-2{border-radius:50px;display:inline-block;height:18px;width:18px}.dot-2{background-color:#add8e6}.dot-3{background-color:orange}.dot-3,.dot-4{border-radius:50px;display:inline-block;height:18px;width:18px}.dot-4{background-color:#f0f}.backbt{background:#a12c2f;border-radius:2px;color:#fff;float:right;font-size:18px;height:40px;line-height:40px;text-align:center;width:80px}.backbt:hover{background:linear-gradient(135deg,#a12c2f,#e43c42)}", "",{"version":3,"sources":["webpack://./resources/js/pages/contents/Content_categorie.vue"],"names":[],"mappings":"AAuGA,SACA,aACA,CACA,kBAMA,8BACA,CACA,QAEA,YAAA,CADA,gBAEA,CACA,cAGA,eAAA,CAFA,MAAA,CACA,aAEA,CACA,mBACA,YAAA,CACA,UACA,CACA,uBACA,WAAA,CAEA,mBAAA,CAAA,gBAAA,CADA,UAEA,CACA,uBACA,iBACA,CACA,sBACA,cAAA,CACA,eACA,CACA,0BAGA,aAAA,CAFA,cAAA,CACA,eAAA,CAEA,gBACA,CACA,iBAEA,aAAA,CADA,kBAEA,CACA,2BACA,kBAAA,CAEA,WAAA,CAIA,iBAAA,CALA,UAAA,CAMA,cAAA,CAHA,cAAA,CADA,YAAA,CAEA,eAAA,CAGA,cACA,CACA,iCACA,mBACA,CACA,OAIA,wBAEA,CACA,cAJA,kBAAA,CAEA,oBAAA,CAJA,WAAA,CACA,UAWA,CANA,OAIA,wBAEA,CACA,OAIA,uBAEA,CACA,cAJA,kBAAA,CAEA,oBAAA,CAJA,WAAA,CACA,UAWA,CANA,OAIA,qBAEA,CACA,QAGA,kBAAA,CACA,iBAAA,CAIA,UAAA,CAHA,WAAA,CAIA,cAAA,CAPA,WAAA,CAKA,gBAAA,CADA,iBAAA,CALA,UASA,CACA,cACA,kDACA","sourcesContent":["<template>\r\n  <div id=\"main\">\r\n    <div id=\"header\">\r\n      <HeaderComponent />\r\n    </div>\r\n\r\n    <div id=\"content\">\r\n      <div v-if=\"this.loading\" class=\"d-flex align-items-center container\">\r\n        <strong>Loading...</strong>\r\n        <div\r\n          class=\"spinner-border ms-auto\"\r\n          role=\"status\"\r\n          aria-hidden=\"true\"\r\n        ></div>\r\n      </div>\r\n      <div v-else class=\"container\">\r\n        <h2 class=\"container\" style=\"text-align: center\">\r\n          หมวดหมู่วิชา: {{ this.posts[0].subjectType }}\r\n        </h2>\r\n        <div class=\"slider owl-carousel mt-5\">\r\n          <div v-for=\"post in posts\" :key=\"post.id\" class=\"card\">\r\n            <div class=\"img\">\r\n              <img :src=\"post.image\" alt=\"\" />\r\n            </div>\r\n            <div class=\"content\">\r\n              <div class=\"title\">{{ post.subjectName }}</div>\r\n\r\n              <div class=\"sub-title\">\r\n                <span class=\"dot-1\"></span> {{ post.subjectType }}\r\n              </div>\r\n              <p :inner-html.prop=\"post.title | truncate(150)\"></p>\r\n              <div class=\"btn\">\r\n                <router-link\r\n                  :to=\"{\r\n                    name: 'course_id',\r\n                    params: { id: post.id },\r\n                  }\"\r\n                  ><button>View more</button></router-link\r\n                >\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n        <a\r\n          class=\"backbt mt-4\"\r\n          @click=\"$router.go(-1)\"\r\n          style=\"text-decoration: none; color: white; cursor: pointer\"\r\n          ><i class=\"fas fa-chevron-left\"></i> Back\r\n        </a>\r\n      </div>\r\n    </div>\r\n\r\n    <div id=\"footer\">\r\n      <FooterComponent />\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport HeaderComponent from \"../../components/HeaderComponent\";\r\nimport FooterComponent from \"../../components/FooterComponent\";\r\nexport default {\r\n  name: \"lessonRecent\",\r\n  components: { HeaderComponent, FooterComponent },\r\n  data() {\r\n    return {\r\n      posts: [],\r\n      loading: true,\r\n    };\r\n  },\r\n  mounted() {\r\n    this.DataPost();\r\n  },\r\n  methods: {\r\n    DataPost() {\r\n      axios\r\n        .get(\"/api/content_category/\"+ this.$route.params.id)\r\n        .then((res) => {\r\n          this.posts = res.data;\r\n          this.loading = false;\r\n        //   console.log(res.data);\r\n        })\r\n        .catch((err) => {\r\n          this.error = \"Error!!\";\r\n        });\r\n    },\r\n  },\r\n};\r\n\r\nVue.filter(\"truncate\", function (value, length) {\r\n  if (!value) return \"\";\r\n  value = value.toString();\r\n  if (value.length > length) {\r\n    return value.substring(0, length) + \"...\";\r\n  } else {\r\n    return value;\r\n  }\r\n});\r\n</script>\r\n\r\n<style>\r\n/* ##### >> https://fonts.google.com/specimen/Sarabun?query=sara#standard-styles */\r\n@import url(\"https://fonts.googleapis.com/css2?family=Sarabun:wght@300&display=swap\");\r\n#content {\r\n  margin-top: 9%;\r\n}\r\nh1,\r\nh2,\r\nh3,\r\nh4,\r\nh5,\r\nh6 {\r\n  font-family: \"Sarabun\", sans-serif;\r\n}\r\n.slider {\r\n  max-width: 1100px;\r\n  display: flex;\r\n}\r\n.slider .card {\r\n  flex: 1;\r\n  margin: 0 10px;\r\n  background: #fff;\r\n}\r\n.slider .card .img {\r\n  height: 200px;\r\n  width: 100%;\r\n}\r\n.slider .card .img img {\r\n  height: 100%;\r\n  width: 100%;\r\n  object-fit: cover;\r\n}\r\n.slider .card .content {\r\n  padding: 10px 20px;\r\n}\r\n.card .content .title {\r\n  font-size: 25px;\r\n  font-weight: 600;\r\n}\r\n.card .content .sub-title {\r\n  font-size: 20px;\r\n  font-weight: 600;\r\n  color: #e74c3c;\r\n  line-height: 20px;\r\n}\r\n.card .content p {\r\n  text-align: justify;\r\n  margin: 10px 0;\r\n}\r\n.card .content .btn button {\r\n  background: #f36656;\r\n  color: #fff;\r\n  border: none;\r\n  outline: none;\r\n  font-size: 17px;\r\n  padding: 5px 8px;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n  transition: 0.3s;\r\n}\r\n.card .content .btn button:hover {\r\n  transform: scale(0.9);\r\n}\r\n.dot-1 {\r\n  height: 18px;\r\n  width: 18px;\r\n  border-radius: 50px;\r\n  background-color: #fe2929;\r\n  display: inline-block;\r\n}\r\n.dot-2 {\r\n  height: 18px;\r\n  width: 18px;\r\n  border-radius: 50px;\r\n  background-color: #add8e6;\r\n  display: inline-block;\r\n}\r\n.dot-3 {\r\n  height: 18px;\r\n  width: 18px;\r\n  border-radius: 50px;\r\n  background-color: #ffa500;\r\n  display: inline-block;\r\n}\r\n.dot-4 {\r\n  height: 18px;\r\n  width: 18px;\r\n  border-radius: 50px;\r\n  background-color: #ff00ff;\r\n  display: inline-block;\r\n}\r\n.backbt {\r\n  width: 80px;\r\n  height: 40px;\r\n  background: #a12c2f;\r\n  border-radius: 2px;\r\n  float: right;\r\n  text-align: center;\r\n  line-height: 40px;\r\n  color: white;\r\n  font-size: 18px;\r\n}\r\n.backbt:hover {\r\n  background: linear-gradient(135deg, #a12c2f, #e43c42);\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
